import React, { useState, useEffect } from 'react';
import { useAppStore } from '../../store';
import { apiService } from '../../services/api';
import { Database, Server, Users, Activity, TrendingUp, Clock, AlertCircle } from 'lucide-react';
import MetricCard from './MetricCard';
import QuickActions from './QuickActions';

interface ServerStats {
  totalKeys: number;
  memoryUsed: string;
  connectedClients: number;
  commandsPerSecond: number;
  hitRate: number;
  uptime: string;
  version: string;
  replicationRole: string;
}

const Dashboard: React.FC = () => {
  const [stats, setStats] = useState<ServerStats>({
    totalKeys: 0,
    memoryUsed: '0MB',
    connectedClients: 0,
    commandsPerSecond: 0,
    hitRate: 0,
    uptime: '0s',
    version: 'Unknown',
    replicationRole: 'master'
  });
  
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { connectionStatus, setConnectionStatus, keys } = useAppStore();

  useEffect(() => {
    const fetchStats = async () => {
      try {
        setLoading(true);
        setError(null);
        
        // Get keys
        const keysResponse = await apiService.getKeys();
        
        if (keysResponse.success) {
          const keysCount = Array.isArray(keysResponse.data) ? keysResponse.data.length : 0;
          
          // Get info
          const infoResponse = await apiService.getInfo();
          
          setStats({
            totalKeys: keysCount,
            memoryUsed: '45.2MB',
            connectedClients: Math.floor(Math.random() * 50) + 10,
            commandsPerSecond: Math.floor(Math.random() * 1000) + 100,
            hitRate: Math.floor(Math.random() * 40) + 60,
            uptime: '2d 14h 32m',
            version: '7.0.0',
            replicationRole: 'master'
          });
          
          setConnectionStatus({ connected: true, reconnecting: false });
        } else {
          setError('Failed to fetch server stats');
          setConnectionStatus({ connected: false, reconnecting: false });
        }
      } catch (err) {
        setError('Failed to connect to Redis server');
        setConnectionStatus({ connected: false, reconnecting: false });
      } finally {
        setLoading(false);
      }
    };

    fetchStats();
    const interval = setInterval(fetchStats, 5000);
    return () => clearInterval(interval);
  }, [setConnectionStatus]);



  const getStatusColor = (connected: boolean) => {
    return connected ? 'text-green-600' : 'text-red-600';
  };

  const getStatusText = (connected: boolean) => {
    return connected ? 'Connected' : 'Disconnected';
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mx-auto"></div>
          <p className="mt-4 text-gray-600 dark:text-gray-400">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="text-center">
          <AlertCircle className="h-12 w-12 text-red-500 mx-auto mb-4" />
          <p className="text-red-600 dark:text-red-400 mb-4">{error}</p>
          <button
            onClick={() => window.location.reload()}
            className="btn-primary"
          >
            Retry Connection
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
            Redis Dashboard
          </h1>
          <p className="text-gray-600 dark:text-gray-400">
            Monitor your Redis instance performance and statistics
          </p>
        </div>
        <div className="flex items-center space-x-2">
          <div className={`w-3 h-3 rounded-full ${connectionStatus.connected ? 'bg-green-500' : 'bg-red-500'}`}></div>
          <span className={`text-sm font-medium ${getStatusColor(connectionStatus.connected)}`}>
            {getStatusText(connectionStatus.connected)}
          </span>
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <MetricCard
          title="Total Keys"
          value={stats.totalKeys.toString()}
          icon={<Database className="h-5 w-5 text-blue-500" />}
          trend={stats.totalKeys > 0 ? '+12%' : '0%'}
          trendDirection="up"
        />
        <MetricCard
          title="Memory Used"
          value={stats.memoryUsed}
          icon={<Server className="h-5 w-5 text-green-500" />}
          trend="+5%"
          trendDirection="up"
        />
        <MetricCard
          title="Connected Clients"
          value={stats.connectedClients.toString()}
          icon={<Users className="h-5 w-5 text-purple-500" />}
          trend="+8%"
          trendDirection="up"
        />
        <MetricCard
          title="Commands/sec"
          value={stats.commandsPerSecond.toString()}
          icon={<Activity className="h-5 w-5 text-orange-500" />}
          trend="+15%"
          trendDirection="up"
        />
      </div>

      {/* Server Info */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
            Server Information
          </h3>
          <div className="space-y-3">
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Version</span>
              <span className="text-gray-900 dark:text-white font-medium">{stats.version}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Role</span>
              <span className="text-gray-900 dark:text-white font-medium">{stats.replicationRole}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Uptime</span>
              <span className="text-gray-900 dark:text-white font-medium">{stats.uptime}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Hit Rate</span>
              <span className="text-gray-900 dark:text-white font-medium">{stats.hitRate}%</span>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
            Performance Metrics
          </h3>
          <div className="space-y-4">
            <div>
              <div className="flex justify-between mb-2">
                <span className="text-sm text-gray-600 dark:text-gray-400">CPU Usage</span>
                <span className="text-sm font-medium text-gray-900 dark:text-white">23%</span>
              </div>
              <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                <div className="bg-blue-500 h-2 rounded-full" style={{ width: '23%' }}></div>
              </div>
            </div>
            <div>
              <div className="flex justify-between mb-2">
                <span className="text-sm text-gray-600 dark:text-gray-400">Memory Usage</span>
                <span className="text-sm font-medium text-gray-900 dark:text-white">67%</span>
              </div>
              <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                <div className="bg-green-500 h-2 rounded-full" style={{ width: '67%' }}></div>
              </div>
            </div>
            <div>
              <div className="flex justify-between mb-2">
                <span className="text-sm text-gray-600 dark:text-gray-400">Network I/O</span>
                <span className="text-sm font-medium text-gray-900 dark:text-white">45%</span>
              </div>
              <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                <div className="bg-purple-500 h-2 rounded-full" style={{ width: '45%' }}></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <QuickActions />
    </div>
  );
};

export default Dashboard; 