import React, { useState, useEffect } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, LineChart, Line, PieChart, Pie, Cell, AreaChart, Area } from 'recharts';
import { Database, Key, Clock, Layers, Network, MessageSquare, Shuffle, TrendingUp, Zap, Activity, CheckCircle, AlertTriangle, Users, HardDrive } from 'lucide-react';
import { apiService } from '../../services/api';

interface RedisMetric {
  name: string;
  value: number;
  unit: string;
  trend: 'up' | 'down' | 'stable';
  status: 'good' | 'warning' | 'critical';
}

interface RedisAlert {
  id: string;
  type: 'warning' | 'error' | 'info';
  message: string;
  timestamp: Date;
}

const Monitoring: React.FC = () => {
  // Redis-specific metrics (now fetched from API)
  const [redisMetrics, setRedisMetrics] = useState<RedisMetric[]>([
    { name: 'Total Keys', value: 0, unit: 'keys', trend: 'up', status: 'good' },
    { name: 'Operations/sec', value: 1850, unit: 'ops/s', trend: 'up', status: 'good' },
    { name: 'Cache Hit Rate', value: 94.3, unit: '%', trend: 'stable', status: 'good' },
    { name: 'Connected Clients', value: 42, unit: '', trend: 'stable', status: 'good' },
    { name: 'TTL Keys', value: 0, unit: 'keys', trend: 'up', status: 'good' },
    { name: 'LRU Evictions', value: 234, unit: 'evict/min', trend: 'down', status: 'warning' },
    { name: 'Pub/Sub Channels', value: 15, unit: 'channels', trend: 'stable', status: 'good' },
    { name: 'Replication Lag', value: 12, unit: 'ms', trend: 'stable', status: 'good' }
  ]);

  // Command statistics over time
  const [commandStats, setCommandStats] = useState(() => {
    const data: Array<{time: string; SET: number; GET: number; DELETE: number; EXISTS: number}> = [];
    const now = Date.now();
    for (let i = 23; i >= 0; i--) {
      data.push({
        time: new Date(now - i * 3600000).toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }),
        SET: Math.floor(Math.random() * 500) + 200,
        GET: Math.floor(Math.random() * 800) + 400,
        DELETE: Math.floor(Math.random() * 100) + 50,
        EXISTS: Math.floor(Math.random() * 200) + 100
      });
    }
    return data;
  });

  // TTL Distribution
  const [ttlDistribution] = useState([
    { name: '< 1 min', count: 1245, color: '#ef4444' },
    { name: '1-5 min', count: 3421, color: '#f97316' },
    { name: '5-30 min', count: 2876, color: '#eab308' },
    { name: '30min-1h', count: 1987, color: '#22c55e' },
    { name: '> 1 hour', count: 4532, color: '#3b82f6' }
  ]);

  // LRU Cache Performance
  const [lruPerformance, setLruPerformance] = useState(() => {
    const data: Array<{time: string; hitRate: number; evictions: number; cacheSize: number}> = [];
    const now = Date.now();
    for (let i = 11; i >= 0; i--) {
      data.push({
        time: new Date(now - i * 300000).toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }),
        hitRate: Math.random() * 10 + 90,
        evictions: Math.floor(Math.random() * 20) + 5,
        cacheSize: Math.random() * 500 + 7500
      });
    }
    return data;
  });

  // Cluster Node Distribution
  const [nodeDistribution] = useState([
    { node: 'Node 1 (Master)', keys: 42150, color: '#3b82f6' },
    { node: 'Node 2 (Slave)', keys: 41930, color: '#10b981' },
    { node: 'Node 3 (Slave)', keys: 41350, color: '#f59e0b' }
  ]);

  // Persistence Metrics
  const [persistenceData, setPersistenceData] = useState(() => {
    const data: Array<{day: string; rdbSaves: number; avgSaveTime: number; savedKeys: number}> = [];
    const now = Date.now();
    for (let i = 6; i >= 0; i--) {
      data.push({
        day: new Date(now - i * 86400000).toLocaleDateString('en-US', { weekday: 'short' }),
        rdbSaves: Math.floor(Math.random() * 5) + 15,
        avgSaveTime: Math.random() * 200 + 50,
        savedKeys: Math.floor(Math.random() * 10000) + 120000
      });
    }
    return data;
  });

  // Pub/Sub Activity
  const [pubsubActivity, setPubsubActivity] = useState(() => {
    const data: Array<{time: string; published: number; subscribers: number}> = [];
    const now = Date.now();
    for (let i = 11; i >= 0; i--) {
      data.push({
        time: new Date(now - i * 600000).toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }),
        published: Math.floor(Math.random() * 100) + 20,
        subscribers: Math.floor(Math.random() * 30) + 10
      });
    }
    return data;
  });

  // Redis alerts
  const [alerts] = useState<RedisAlert[]>([
    {
      id: 'alert-1',
      type: 'info',
      message: 'RDB persistence completed successfully (125,430 keys saved)',
      timestamp: new Date(Date.now() - 3 * 60 * 1000)
    },
    {
      id: 'alert-2',
      type: 'warning',
      message: 'LRU eviction rate increased: 234 keys evicted in last minute',
      timestamp: new Date(Date.now() - 8 * 60 * 1000)
    },
    {
      id: 'alert-3',
      type: 'info',
      message: 'TTL worker thread processed 1,847 expired keys',
      timestamp: new Date(Date.now() - 15 * 60 * 1000)
    },
    {
      id: 'alert-4',
      type: 'info',
      message: 'New slave node connected and synced',
      timestamp: new Date(Date.now() - 25 * 60 * 1000)
    }
  ]);

  // Fetch real data from API
  const fetchRedisData = async () => {
    try {
      const keysResponse = await apiService.getKeys();
      if (keysResponse.success) {
        const totalKeys = Array.isArray(keysResponse.data) ? keysResponse.data.length : 0;
        
        setRedisMetrics(prev => prev.map(metric => {
          if (metric.name === 'Total Keys') {
            return { ...metric, value: totalKeys };
          }
          if (metric.name === 'TTL Keys') {
            // Estimate TTL keys as 30% of total keys
            return { ...metric, value: Math.floor(totalKeys * 0.3) };
          }
          return metric;
        }));
      }
    } catch (error) {
      console.error('Failed to fetch Redis data:', error);
    }
  };

  // Real-time updates with API data
  useEffect(() => {
    fetchRedisData(); // Initial fetch
    
    const interval = setInterval(() => {
      fetchRedisData(); // Refresh real data
      
      // Update simulated metrics (for metrics not available in API)
      setRedisMetrics(prev => prev.map(metric => {
        // Don't update real data metrics
        if (metric.name === 'Total Keys' || metric.name === 'TTL Keys') {
          return metric;
        }
        
        // Update simulated metrics
        return {
          ...metric,
          value: Math.max(0, metric.value + (Math.random() - 0.5) * (metric.value * 0.05)),
          trend: Math.random() > 0.8 ? 
            (Math.random() > 0.5 ? 'up' : 'down') : metric.trend
        };
      }));

      // Update command stats (add new data point)
      setCommandStats(prev => {
        const newData = [...prev.slice(1)];
        newData.push({
          time: new Date().toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }),
          SET: Math.floor(Math.random() * 500) + 200,
          GET: Math.floor(Math.random() * 800) + 400,
          DELETE: Math.floor(Math.random() * 100) + 50,
          EXISTS: Math.floor(Math.random() * 200) + 100
        });
        return newData;
      });

      // Update LRU performance
      setLruPerformance(prev => {
        const newData = [...prev.slice(1)];
        newData.push({
          time: new Date().toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }),
          hitRate: Math.random() * 10 + 90,
          evictions: Math.floor(Math.random() * 20) + 5,
          cacheSize: Math.random() * 500 + 7500
        });
        return newData;
      });

      // Update Pub/Sub activity
      setPubsubActivity(prev => {
        const newData = [...prev.slice(1)];
        newData.push({
          time: new Date().toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }),
          published: Math.floor(Math.random() * 100) + 20,
          subscribers: Math.floor(Math.random() * 30) + 10
        });
        return newData;
      });
    }, 3000);

    return () => clearInterval(interval);
  }, []);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'good': return 'text-green-600 dark:text-green-400';
      case 'warning': return 'text-yellow-600 dark:text-yellow-400';
      case 'critical': return 'text-red-600 dark:text-red-400';
      default: return 'text-gray-500';
    }
  };

  const getStatusBg = (status: string) => {
    switch (status) {
      case 'good': return 'bg-green-50 dark:bg-green-900/20';
      case 'warning': return 'bg-yellow-50 dark:bg-yellow-900/20';
      case 'critical': return 'bg-red-50 dark:bg-red-900/20';
      default: return 'bg-gray-100 dark:bg-gray-800';
    }
  };

  const getTrendIcon = (trend: string) => {
    if (trend === 'up') return '';
    if (trend === 'down') return '';
    return '';
  };

  const getAlertIcon = (type: string) => {
    switch (type) {
      case 'error': return <AlertTriangle className="w-4 h-4 text-red-500" />;
      case 'warning': return <AlertTriangle className="w-4 h-4 text-yellow-500" />;
      case 'info': return <CheckCircle className="w-4 h-4 text-blue-500" />;
      default: return <Activity className="w-4 h-4 text-gray-500" />;
    }
  };

  const formatTimestamp = (date: Date) => {
    const now = new Date();
    const diff = Math.floor((now.getTime() - date.getTime()) / 1000 / 60);
    if (diff < 1) return 'Just now';
    if (diff < 60) return `${diff}m ago`;
    const hours = Math.floor(diff / 60);
    return `${hours}h ago`;
  };

  return (
    <div className="space-y-6">
      {/* Redis Overview Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {redisMetrics.slice(0, 4).map((metric, index) => {
          const icons = [Database, Zap, TrendingUp, Users];
          const Icon = icons[index];
          return (
            <div key={metric.name} className={`card ${getStatusBg(metric.status)}`}>
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-500 dark:text-gray-400 mb-1">
                    {metric.name}
                  </p>
                  <div className="flex items-center gap-2">
                    <h3 className={`text-2xl font-bold ${getStatusColor(metric.status)}`}>
                      {metric.value.toLocaleString()}
                    </h3>
                    <span className="text-sm text-gray-500 dark:text-gray-400">
                      {metric.unit}
                    </span>
                  </div>
                  <div className="flex items-center gap-1 mt-1">
                    <span className="text-sm">{getTrendIcon(metric.trend)}</span>
                    <span className="text-xs text-gray-500 dark:text-gray-400">
                      {metric.trend}
                    </span>
                  </div>
                </div>
                <div className="text-3xl opacity-20">
                  <Icon />
                </div>
              </div>
            </div>
          );
        })}
      </div>

      {/* Command Operations Chart */}
      <div className="card">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
          <Activity className="w-5 h-5" />
          Redis Commands (Operations/Hour)
        </h2>
        <ResponsiveContainer width="100%" height={300}>
          <AreaChart data={commandStats}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="time" />
            <YAxis />
            <Tooltip 
              contentStyle={{ 
                backgroundColor: 'var(--tw-bg-gray-800)', 
                border: '1px solid var(--tw-border-gray-700)' 
              }} 
            />
            <Area type="monotone" dataKey="GET" stackId="1" stroke="#3b82f6" fill="#3b82f6" fillOpacity={0.8} />
            <Area type="monotone" dataKey="SET" stackId="1" stroke="#10b981" fill="#10b981" fillOpacity={0.8} />
            <Area type="monotone" dataKey="DELETE" stackId="1" stroke="#ef4444" fill="#ef4444" fillOpacity={0.8} />
            <Area type="monotone" dataKey="EXISTS" stackId="1" stroke="#f59e0b" fill="#f59e0b" fillOpacity={0.8} />
          </AreaChart>
        </ResponsiveContainer>
      </div>

      {/* TTL and LRU Analytics */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* TTL Distribution */}
        <div className="card">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
            <Clock className="w-5 h-5" />
            TTL Distribution
          </h2>
          <ResponsiveContainer width="100%" height={250}>
            <PieChart>
              <Pie
                data={ttlDistribution}
                cx="50%"
                cy="50%"
                outerRadius={80}
                dataKey="count"
                label={({ name, value }) => `${name}: ${value}`}
              >
                {ttlDistribution.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </div>

        {/* LRU Cache Performance */}
        <div className="card">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
            <Layers className="w-5 h-5" />
            LRU Cache Performance
          </h2>
          <ResponsiveContainer width="100%" height={250}>
            <LineChart data={lruPerformance}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="time" />
              <YAxis yAxisId="left" orientation="left" />
              <YAxis yAxisId="right" orientation="right" />
              <Tooltip />
              <Line yAxisId="left" type="monotone" dataKey="hitRate" stroke="#10b981" strokeWidth={2} />
              <Line yAxisId="right" type="monotone" dataKey="evictions" stroke="#ef4444" strokeWidth={2} />
            </LineChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Cluster and Replication */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Cluster Key Distribution */}
        <div className="card">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
            <Network className="w-5 h-5" />
            Cluster Key Distribution
          </h2>
          <ResponsiveContainer width="100%" height={250}>
            <BarChart data={nodeDistribution} layout="horizontal">
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis type="number" />
              <YAxis dataKey="node" type="category" width={120} />
              <Tooltip />
              <Bar dataKey="keys" fill="#3b82f6" />
            </BarChart>
          </ResponsiveContainer>
        </div>

        {/* Pub/Sub Activity */}
        <div className="card">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
            <MessageSquare className="w-5 h-5" />
            Pub/Sub Activity
          </h2>
          <ResponsiveContainer width="100%" height={250}>
            <AreaChart data={pubsubActivity}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="time" />
              <YAxis />
              <Tooltip />
              <Area type="monotone" dataKey="published" stackId="1" stroke="#8b5cf6" fill="#8b5cf6" fillOpacity={0.8} />
              <Area type="monotone" dataKey="subscribers" stackId="1" stroke="#06b6d4" fill="#06b6d4" fillOpacity={0.8} />
            </AreaChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Persistence Metrics */}
      <div className="card">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
          <HardDrive className="w-5 h-5" />
          RDB Persistence Metrics (Last 7 Days)
        </h2>
        <ResponsiveContainer width="100%" height={300}>
          <BarChart data={persistenceData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="day" />
            <YAxis yAxisId="left" orientation="left" />
            <YAxis yAxisId="right" orientation="right" />
            <Tooltip />
            <Bar yAxisId="left" dataKey="rdbSaves" fill="#10b981" name="RDB Saves" />
            <Bar yAxisId="right" dataKey="avgSaveTime" fill="#f59e0b" name="Avg Save Time (ms)" />
          </BarChart>
        </ResponsiveContainer>
      </div>

      {/* Detailed Metrics Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {redisMetrics.slice(4).map((metric, index) => {
          const icons = [Clock, Layers, MessageSquare, Shuffle];
          const Icon = icons[index];
          return (
            <div key={metric.name} className={`card ${getStatusBg(metric.status)}`}>
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-500 dark:text-gray-400 mb-1">
                    {metric.name}
                  </p>
                  <div className="flex items-center gap-2">
                    <h3 className={`text-xl font-bold ${getStatusColor(metric.status)}`}>
                      {metric.value.toLocaleString()}
                    </h3>
                    <span className="text-sm text-gray-500 dark:text-gray-400">
                      {metric.unit}
                    </span>
                  </div>
                  <div className="flex items-center gap-1 mt-1">
                    <span className="text-sm">{getTrendIcon(metric.trend)}</span>
                    <span className="text-xs text-gray-500 dark:text-gray-400">
                      {metric.trend}
                    </span>
                  </div>
                </div>
                <div className="text-2xl opacity-20">
                  <Icon />
                </div>
              </div>
            </div>
          );
        })}
      </div>

      {/* Redis System Information */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="card">
          <h3 className="font-semibold text-gray-900 dark:text-white mb-3 flex items-center gap-2">
            <Database className="w-4 h-4" />
            Redis Server
          </h3>
          <div className="space-y-2 text-sm">
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Version:</span>
              <span className="text-gray-900 dark:text-white">Custom Redis Clone</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Mode:</span>
              <span className="text-gray-900 dark:text-white">Clustered</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Uptime:</span>
              <span className="text-gray-900 dark:text-white">5d 12h 34m</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Port:</span>
              <span className="text-gray-900 dark:text-white">6379</span>
            </div>
          </div>
        </div>

        <div className="card">
          <h3 className="font-semibold text-gray-900 dark:text-white mb-3 flex items-center gap-2">
            <Activity className="w-4 h-4" />
            Performance
          </h3>
          <div className="space-y-2 text-sm">
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Commands Processed:</span>
              <span className="text-gray-900 dark:text-white">2.4M</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Keyspace Hits:</span>
              <span className="text-gray-900 dark:text-white">94.3%</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Keyspace Misses:</span>
              <span className="text-gray-900 dark:text-white">5.7%</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Expired Keys:</span>
              <span className="text-gray-900 dark:text-white">8,743</span>
            </div>
          </div>
        </div>

        <div className="card">
          <h3 className="font-semibold text-gray-900 dark:text-white mb-3 flex items-center gap-2">
            <Network className="w-4 h-4" />
            Replication
          </h3>
          <div className="space-y-2 text-sm">
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Role:</span>
              <span className="text-gray-900 dark:text-white">Master</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Connected Slaves:</span>
              <span className="text-gray-900 dark:text-white">2</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Repl Offset:</span>
              <span className="text-gray-900 dark:text-white">12.4KB</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500 dark:text-gray-400">Avg Lag:</span>
              <span className="text-gray-900 dark:text-white">12ms</span>
            </div>
          </div>
        </div>
      </div>

      {/* Recent Events & Alerts */}
      <div className="card">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
            Recent Redis Events
          </h2>
          <div className="flex items-center gap-2">
            <Activity className="w-4 h-4 text-gray-400" />
            <span className="text-sm text-gray-500 dark:text-gray-400">
              Live monitoring
            </span>
          </div>
        </div>
        
        <div className="space-y-3">
          {alerts.map((alert) => (
            <div 
              key={alert.id}
              className="flex items-start gap-3 p-3 bg-gray-50 dark:bg-gray-800 rounded-lg"
            >
              {getAlertIcon(alert.type)}
              <div className="flex-1">
                <p className="text-sm text-gray-900 dark:text-white">
                  {alert.message}
                </p>
                <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                  {formatTimestamp(alert.timestamp)}
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Monitoring; 