import React, { useState } from 'react';
import { Server, Activity, Users, Database, AlertCircle, CheckCircle, RefreshCw } from 'lucide-react';

interface ClusterNode {
  id: string;
  host: string;
  port: number;
  role: 'master' | 'slave';
  status: 'connected' | 'disconnected' | 'syncing';
  slots: string;
  connections: number;
  memoryUsage: number;
  keyCount: number;
}

const ClusterView: React.FC = () => {
  const [selectedNode, setSelectedNode] = useState<string | null>(null);

  // Mock cluster data
  const [nodes] = useState<ClusterNode[]>([
    {
      id: 'node-1',
      host: '127.0.0.1',
      port: 7000,
      role: 'master',
      status: 'connected',
      slots: '0-5460',
      connections: 12,
      memoryUsage: 45.2,
      keyCount: 15420
    },
    {
      id: 'node-2',
      host: '127.0.0.1',
      port: 7001,
      role: 'slave',
      status: 'connected',
      slots: '0-5460',
      connections: 8,
      memoryUsage: 23.1,
      keyCount: 15420
    },
    {
      id: 'node-3',
      host: '127.0.0.1',
      port: 7002,
      role: 'master',
      status: 'connected',
      slots: '5461-10922',
      connections: 15,
      memoryUsage: 52.8,
      keyCount: 18750
    },
    {
      id: 'node-4',
      host: '127.0.0.1',
      port: 7003,
      role: 'slave',
      status: 'syncing',
      slots: '5461-10922',
      connections: 3,
      memoryUsage: 31.4,
      keyCount: 18750
    },
    {
      id: 'node-5',
      host: '127.0.0.1',
      port: 7004,
      role: 'master',
      status: 'connected',
      slots: '10923-16383',
      connections: 9,
      memoryUsage: 38.7,
      keyCount: 12300
    },
    {
      id: 'node-6',
      host: '127.0.0.1',
      port: 7005,
      role: 'slave',
      status: 'disconnected',
      slots: '10923-16383',
      connections: 0,
      memoryUsage: 0,
      keyCount: 0
    }
  ]);

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'connected':
        return <CheckCircle className="w-4 h-4 text-success-500" />;
      case 'disconnected':
        return <AlertCircle className="w-4 h-4 text-error-500" />;
      case 'syncing':
        return <RefreshCw className="w-4 h-4 text-warning-500 animate-spin" />;
      default:
        return <AlertCircle className="w-4 h-4 text-gray-400" />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'connected':
        return 'text-success-500';
      case 'disconnected':
        return 'text-error-500';
      case 'syncing':
        return 'text-warning-500';
      default:
        return 'text-gray-400';
    }
  };

  const masterNodes = nodes.filter(node => node.role === 'master');
  const totalKeys = nodes.reduce((sum, node) => sum + node.keyCount, 0);
  const avgMemory = nodes.reduce((sum, node) => sum + node.memoryUsage, 0) / nodes.length;

  return (
    <div className="space-y-6">
      {/* Cluster Overview */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center gap-3">
            <Server className="w-8 h-8 text-primary-500" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
                {nodes.length}
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400">Total Nodes</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center gap-3">
            <Activity className="w-8 h-8 text-success-500" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
                {nodes.filter(n => n.status === 'connected').length}
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400">Active Nodes</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center gap-3">
            <Database className="w-8 h-8 text-accent-500" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
                {totalKeys.toLocaleString()}
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400">Total Keys</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center gap-3">
            <Users className="w-8 h-8 text-secondary-500" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
                {avgMemory.toFixed(1)}%
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400">Avg Memory</p>
            </div>
          </div>
        </div>
      </div>

      {/* Cluster Topology */}
      <div className="card">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-gray-100 mb-6">
          Cluster Topology
        </h2>
        
        <div className="space-y-6">
          {masterNodes.map((master) => {
            const slaves = nodes.filter(node => 
              node.role === 'slave' && node.slots === master.slots
            );
            
            return (
              <div key={master.id} className="border border-gray-200 dark:border-gray-700 rounded-lg p-4">
                <div className="flex items-center justify-between mb-4">
                  <h3 className="font-semibold text-gray-900 dark:text-gray-100">
                    Shard: {master.slots}
                  </h3>
                  <span className="text-sm text-gray-500 dark:text-gray-400">
                    {slaves.length + 1} nodes
                  </span>
                </div>
                
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {/* Master Node */}
                  <div 
                    className={`p-4 border-2 rounded-lg cursor-pointer transition-all ${
                      selectedNode === master.id 
                        ? 'border-primary-500 bg-primary-50 dark:bg-primary-900/20' 
                        : 'border-gray-200 dark:border-gray-700 hover:border-gray-300 dark:hover:border-gray-600'
                    }`}
                    onClick={() => setSelectedNode(master.id)}
                  >
                    <div className="flex items-center justify-between mb-2">
                      <div className="flex items-center gap-2">
                        {getStatusIcon(master.status)}
                        <span className="text-sm font-medium text-gray-900 dark:text-gray-100">
                          MASTER
                        </span>
                      </div>
                      <span className={`text-sm font-medium ${getStatusColor(master.status)}`}>
                        {master.status.toUpperCase()}
                      </span>
                    </div>
                    
                    <div className="space-y-1 text-sm">
                      <div className="font-mono text-gray-900 dark:text-gray-100">
                        {master.host}:{master.port}
                      </div>
                      <div className="text-gray-500 dark:text-gray-400">
                        Memory: {master.memoryUsage}%
                      </div>
                      <div className="text-gray-500 dark:text-gray-400">
                        Keys: {master.keyCount.toLocaleString()}
                      </div>
                      <div className="text-gray-500 dark:text-gray-400">
                        Connections: {master.connections}
                      </div>
                    </div>
                  </div>

                  {/* Slave Nodes */}
                  {slaves.map((slave) => (
                    <div 
                      key={slave.id}
                      className={`p-4 border-2 rounded-lg cursor-pointer transition-all ${
                        selectedNode === slave.id 
                          ? 'border-primary-500 bg-primary-50 dark:bg-primary-900/20' 
                          : 'border-gray-200 dark:border-gray-700 hover:border-gray-300 dark:hover:border-gray-600'
                      }`}
                      onClick={() => setSelectedNode(slave.id)}
                    >
                      <div className="flex items-center justify-between mb-2">
                        <div className="flex items-center gap-2">
                          {getStatusIcon(slave.status)}
                          <span className="text-sm font-medium text-gray-900 dark:text-gray-100">
                            SLAVE
                          </span>
                        </div>
                        <span className={`text-sm font-medium ${getStatusColor(slave.status)}`}>
                          {slave.status.toUpperCase()}
                        </span>
                      </div>
                      
                      <div className="space-y-1 text-sm">
                        <div className="font-mono text-gray-900 dark:text-gray-100">
                          {slave.host}:{slave.port}
                        </div>
                        <div className="text-gray-500 dark:text-gray-400">
                          Memory: {slave.memoryUsage}%
                        </div>
                        <div className="text-gray-500 dark:text-gray-400">
                          Keys: {slave.keyCount.toLocaleString()}
                        </div>
                        <div className="text-gray-500 dark:text-gray-400">
                          Connections: {slave.connections}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* Node Details */}
      {selectedNode && (
        <div className="card">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-gray-100 mb-4">
            Node Details: {selectedNode}
          </h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {(() => {
              const node = nodes.find(n => n.id === selectedNode)!;
              return (
                <>
                  <div>
                    <h3 className="font-medium text-gray-900 dark:text-gray-100 mb-3">
                      Connection Info
                    </h3>
                    <div className="space-y-2 text-sm">
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Host:</span>
                        <span className="font-mono text-gray-900 dark:text-gray-100">{node.host}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Port:</span>
                        <span className="font-mono text-gray-900 dark:text-gray-100">{node.port}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Role:</span>
                        <span className="text-gray-900 dark:text-gray-100">{node.role}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Status:</span>
                        <span className={getStatusColor(node.status)}>{node.status}</span>
                      </div>
                    </div>
                  </div>
                  
                  <div>
                    <h3 className="font-medium text-gray-900 dark:text-gray-100 mb-3">
                      Performance
                    </h3>
                    <div className="space-y-2 text-sm">
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Slots:</span>
                        <span className="font-mono text-gray-900 dark:text-gray-100">{node.slots}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Keys:</span>
                        <span className="text-gray-900 dark:text-gray-100">{node.keyCount.toLocaleString()}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Memory Usage:</span>
                        <span className="text-gray-900 dark:text-gray-100">{node.memoryUsage}%</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-gray-500 dark:text-gray-400">Connections:</span>
                        <span className="text-gray-900 dark:text-gray-100">{node.connections}</span>
                      </div>
                    </div>
                  </div>
                </>
              );
            })()}
          </div>
        </div>
      )}
    </div>
  );
};

export default ClusterView; 