  async getNodes(): Promise<ApiResponse<RedisNode[]>> {
    try {
      const response = await this.client.get('/api/info');
      const info = response.data.success ? response.data.data : response.data;
      
      // Transform the nodes info into the expected format
      const nodes: RedisNode[] = [];
      if (info.nodes) {
        for (const [nodeName, nodeInfo] of Object.entries(info.nodes)) {
          nodes.push({
            id: nodeName,
            name: nodeName,
            status: 'healthy',
            host: 'localhost',
            port: 6379,
            memory: {
              used: 0,
              total: 0,
              percentage: 0
            },
            connections: 0,
            keyCount: (nodeInfo as any).keys,
            hitRate: 0,
            lastSeen: new Date(),
          });
        }
      }
      
      return {
        success: true,
        data: nodes,
        timestamp: new Date(),
      };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Failed to get nodes',
        timestamp: new Date(),
      };
    }
  }

