import React, { useState, useEffect } from 'react';
import { useAppStore } from '../../store';
import { Timer, Play, Pause, RotateCcw, Plus, Trash2, Clock } from 'lucide-react';
import TTLKeyCard from './TTLKeyCard';

const TTLPlayground: React.FC = () => {
  const { ttlEntries, setTTLEntries, addNotification } = useAppStore();
  const [isRunning, setIsRunning] = useState(true);
  const [newKeyName, setNewKeyName] = useState('');
  const [newKeyValue, setNewKeyValue] = useState('');
  const [newKeyTTL, setNewKeyTTL] = useState(30);

  useEffect(() => {
    if (!isRunning) return;

    const interval = setInterval(() => {
      setTTLEntries(
        ttlEntries.map(entry => {
          const now = Date.now();
          const elapsed = now - entry.startTime.getTime();
          const remainingTime = Math.max(0, entry.ttl - elapsed / 1000);
          const progress = Math.min(100, (elapsed / 1000 / entry.ttl) * 100);

          if (remainingTime <= 0) {
            addNotification({
              type: 'warning',
              title: 'Key Expired',
              message: `Key "${entry.key}" has expired and been removed`,
            });
            return null;
          }

          return {
            ...entry,
            remainingTime,
            progress,
          };
        }).filter(Boolean) as any[]
      );
    }, 1000);

    return () => clearInterval(interval);
  }, [isRunning, ttlEntries, setTTLEntries, addNotification]);

  const handleAddKey = () => {
    if (!newKeyName.trim()) {
      addNotification({
        type: 'error',
        title: 'Invalid Key',
        message: 'Key name cannot be empty',
      });
      return;
    }

    if (ttlEntries.some(entry => entry.key === newKeyName)) {
      addNotification({
        type: 'error',
        title: 'Key Exists',
        message: 'A key with this name already exists',
      });
      return;
    }

    const newEntry = {
      key: newKeyName,
      value: newKeyValue || 'Sample value',
      ttl: newKeyTTL,
      startTime: new Date(),
      remainingTime: newKeyTTL,
      progress: 0,
    };

    setTTLEntries([...ttlEntries, newEntry]);
    setNewKeyName('');
    setNewKeyValue('');
    setNewKeyTTL(30);

    addNotification({
      type: 'success',
      title: 'Key Added',
      message: `Key "${newKeyName}" added with ${newKeyTTL}s TTL`,
    });
  };

  const handleDeleteKey = (keyName: string) => {
    setTTLEntries(ttlEntries.filter(entry => entry.key !== keyName));
    addNotification({
      type: 'info',
      title: 'Key Deleted',
      message: `Key "${keyName}" has been manually deleted`,
    });
  };

  const handleResetKey = (keyName: string) => {
    setTTLEntries(
      ttlEntries.map(entry =>
        entry.key === keyName
          ? {
              ...entry,
              startTime: new Date(),
              remainingTime: entry.ttl,
              progress: 0,
            }
          : entry
      )
    );
    addNotification({
      type: 'info',
      title: 'TTL Reset',
      message: `TTL for key "${keyName}" has been reset`,
    });
  };

  const handleClearAll = () => {
    setTTLEntries([]);
    addNotification({
      type: 'info',
      title: 'All Keys Cleared',
      message: 'All TTL entries have been cleared',
    });
  };

  const addSampleKeys = () => {
    const sampleKeys = [
      { key: 'session:user123', value: 'user_data', ttl: 15 },
      { key: 'cache:product456', value: 'product_info', ttl: 60 },
      { key: 'temp:upload789', value: 'file_metadata', ttl: 30 },
      { key: 'rate:limit:ip', value: 'request_count', ttl: 10 },
    ];

    const newEntries = sampleKeys.map(sample => ({
      ...sample,
      startTime: new Date(),
      remainingTime: sample.ttl,
      progress: 0,
    }));

    setTTLEntries([...ttlEntries, ...newEntries]);
    addNotification({
      type: 'success',
      title: 'Sample Keys Added',
      message: 'Added 4 sample keys with different TTL values',
    });
  };

  const expiringKeys = ttlEntries.filter(entry => entry.remainingTime <= 5);
  const avgTTL = ttlEntries.length > 0 
    ? ttlEntries.reduce((sum, entry) => sum + entry.remainingTime, 0) / ttlEntries.length 
    : 0;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white flex items-center gap-3">
            <Timer className="text-orange-500" size={32} />
            TTL Playground
          </h1>
          <p className="text-gray-600 dark:text-gray-400 mt-1">
            Interactive demonstration of Redis TTL (Time-To-Live) functionality
          </p>
        </div>
        <div className="flex items-center space-x-3">
          <div className="flex items-center space-x-2 text-sm text-gray-600 dark:text-gray-400">
            <Clock size={16} />
            <span>Avg TTL: {avgTTL.toFixed(1)}s</span>
          </div>
          <button
            onClick={() => setIsRunning(!isRunning)}
            className={`flex items-center space-x-2 px-4 py-2 rounded-md font-medium transition-colors ${
              isRunning 
                ? 'bg-red-500 hover:bg-red-600 text-white' 
                : 'bg-green-500 hover:bg-green-600 text-white'
            }`}
          >
            {isRunning ? <Pause size={16} /> : <Play size={16} />}
            <span>{isRunning ? 'Pause' : 'Resume'}</span>
          </button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-gray-600 dark:text-gray-400">Total Keys</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">
                {ttlEntries.length}
              </p>
            </div>
            <div className="p-3 bg-blue-100 dark:bg-blue-900/20 rounded-lg">
              <Timer className="text-blue-600 dark:text-blue-400" size={24} />
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-gray-600 dark:text-gray-400">Expiring Soon</p>
              <p className="text-2xl font-bold text-orange-600 dark:text-orange-400">
                {expiringKeys.length}
              </p>
            </div>
            <div className="p-3 bg-orange-100 dark:bg-orange-900/20 rounded-lg">
              <Clock className="text-orange-600 dark:text-orange-400" size={24} />
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-gray-600 dark:text-gray-400">Status</p>
              <p className="text-2xl font-bold text-green-600 dark:text-green-400">
                {isRunning ? 'Running' : 'Paused'}
              </p>
            </div>
            <div className="p-3 bg-green-100 dark:bg-green-900/20 rounded-lg">
              {isRunning ? (
                <Play className="text-green-600 dark:text-green-400" size={24} />
              ) : (
                <Pause className="text-gray-600 dark:text-gray-400" size={24} />
              )}
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-gray-600 dark:text-gray-400">Avg Remaining</p>
              <p className="text-2xl font-bold text-purple-600 dark:text-purple-400">
                {avgTTL.toFixed(1)}s
              </p>
            </div>
            <div className="p-3 bg-purple-100 dark:bg-purple-900/20 rounded-lg">
              <RotateCcw className="text-purple-600 dark:text-purple-400" size={24} />
            </div>
          </div>
        </div>
      </div>

      {/* Controls */}
      <div className="card">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-gray-100 mb-4">
          Add New TTL Key
        </h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
              Key Name
            </label>
            <input
              type="text"
              value={newKeyName}
              onChange={(e) => setNewKeyName(e.target.value)}
              placeholder="e.g., session:user123"
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
              Value
            </label>
            <input
              type="text"
              value={newKeyValue}
              onChange={(e) => setNewKeyValue(e.target.value)}
              placeholder="Key value (optional)"
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
              TTL (seconds)
            </label>
            <input
              type="number"
              value={newKeyTTL}
              onChange={(e) => setNewKeyTTL(Number(e.target.value))}
              min={1}
              max={300}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            />
          </div>
        </div>
        <div className="flex gap-3">
          <button onClick={handleAddKey} className="btn-primary flex items-center gap-2">
            <Plus className="w-4 h-4" />
            Add Key
          </button>
          <button onClick={addSampleKeys} className="btn-secondary flex items-center gap-2">
            <Timer className="w-4 h-4" />
            Add Samples
          </button>
          <button onClick={handleClearAll} className="btn-secondary flex items-center gap-2 text-red-600">
            <Trash2 className="w-4 h-4" />
            Clear All
          </button>
        </div>
      </div>

      {/* Keys Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {ttlEntries.map((entry) => (
          <TTLKeyCard
            key={entry.key}
            entry={entry}
            onDelete={handleDeleteKey}
            onReset={handleResetKey}
          />
        ))}
      </div>

      {/* Empty State */}
      {ttlEntries.length === 0 && (
        <div className="text-center py-12">
          <Timer className="mx-auto text-gray-400 dark:text-gray-600 mb-4" size={64} />
          <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
            No TTL Keys
          </h3>
          <p className="text-gray-600 dark:text-gray-400 mb-4">
            Add some keys with TTL values to see them expire in real-time
          </p>
          <button
            onClick={addSampleKeys}
            className="btn-primary"
          >
            Add Sample Keys
          </button>
        </div>
      )}
    </div>
  );
};

export default TTLPlayground; 