import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAppStore } from '../../store';
import { Plus, Search, RefreshCw, Settings, Download } from 'lucide-react';

const QuickActions: React.FC = () => {
  const navigate = useNavigate();
  const { addNotification } = useAppStore();

  const handleAddKey = () => {
    navigate('/keys');
    addNotification({
      type: 'info',
      title: 'Add Key',
      message: 'Navigate to Key Explorer to add new keys',
    });
  };

  const handleRefreshData = () => {
    // This would trigger a data refresh
    addNotification({
      type: 'success',
      title: 'Data Refreshed',
      message: 'All data has been refreshed successfully',
    });
  };

  const handleExportData = () => {
    addNotification({
      type: 'info',
      title: 'Export Started',
      message: 'Data export is being prepared...',
    });
  };

  return (
    <div className="card">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-lg font-semibold text-gray-900 dark:text-white">
          Quick Actions
        </h2>
      </div>
      
      <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4">
        <button
          onClick={handleAddKey}
          className="flex flex-col items-center space-y-2 p-4 rounded-lg bg-blue-50 dark:bg-blue-900/20 hover:bg-blue-100 dark:hover:bg-blue-900/40 transition-colors group"
        >
          <Plus size={24} className="text-blue-600 dark:text-blue-400 group-hover:scale-110 transition-transform" />
          <span className="text-sm font-medium text-blue-700 dark:text-blue-300">
            Add Key
          </span>
        </button>

        <button
          onClick={() => navigate('/keys')}
          className="flex flex-col items-center space-y-2 p-4 rounded-lg bg-green-50 dark:bg-green-900/20 hover:bg-green-100 dark:hover:bg-green-900/40 transition-colors group"
        >
          <Search size={24} className="text-green-600 dark:text-green-400 group-hover:scale-110 transition-transform" />
          <span className="text-sm font-medium text-green-700 dark:text-green-300">
            Browse Keys
          </span>
        </button>

        <button
          onClick={handleRefreshData}
          className="flex flex-col items-center space-y-2 p-4 rounded-lg bg-orange-50 dark:bg-orange-900/20 hover:bg-orange-100 dark:hover:bg-orange-900/40 transition-colors group"
        >
          <RefreshCw size={24} className="text-orange-600 dark:text-orange-400 group-hover:scale-110 transition-transform group-hover:rotate-180" />
          <span className="text-sm font-medium text-orange-700 dark:text-orange-300">
            Refresh
          </span>
        </button>

        <button
          onClick={() => navigate('/monitoring')}
          className="flex flex-col items-center space-y-2 p-4 rounded-lg bg-purple-50 dark:bg-purple-900/20 hover:bg-purple-100 dark:hover:bg-purple-900/40 transition-colors group"
        >
          <Settings size={24} className="text-purple-600 dark:text-purple-400 group-hover:scale-110 transition-transform" />
          <span className="text-sm font-medium text-purple-700 dark:text-purple-300">
            Monitor
          </span>
        </button>

        <button
          onClick={handleExportData}
          className="flex flex-col items-center space-y-2 p-4 rounded-lg bg-indigo-50 dark:bg-indigo-900/20 hover:bg-indigo-100 dark:hover:bg-indigo-900/40 transition-colors group"
        >
          <Download size={24} className="text-indigo-600 dark:text-indigo-400 group-hover:scale-110 transition-transform" />
          <span className="text-sm font-medium text-indigo-700 dark:text-indigo-300">
            Export
          </span>
        </button>

        <button
          onClick={() => navigate('/ttl')}
          className="flex flex-col items-center space-y-2 p-4 rounded-lg bg-pink-50 dark:bg-pink-900/20 hover:bg-pink-100 dark:hover:bg-pink-900/40 transition-colors group"
        >
          <span className="text-2xl group-hover:scale-110 transition-transform"></span>
          <span className="text-sm font-medium text-pink-700 dark:text-pink-300">
            TTL Demo
          </span>
        </button>
      </div>
    </div>
  );
};

export default QuickActions; 